import { useEffect, useRef } from "react";
import style from './Section04.module.css';
import sec05Img from '../images/section05_img.png';
import gsap from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

gsap.registerPlugin(ScrollTrigger);

function Section05() {

    const txtRef = useRef();

    useEffect(() => {
        gsap.fromTo(
            txtRef.current,
            {
                opacity: 0,
                y: 50,
            },
            {
                opacity: 1,
                y: 0,
                duration: 1.5,
                ease: "power2.out",
                scrollTrigger: {
                    trigger: txtRef.current,
                    start: "top 80%",
                    toggleActions: "play none none none",
                    // markers: true,
                },
            }
        );
    }, []);

    return (
        <div className={style.container}>
            <div className={`${style.secBox} ${style.secBox02}`}>
                <div className={style.secTxt} ref={txtRef}>
                    <h3>
                        <br />
                        CS
                    </h3>
                    <p>
                        K-PUFFU    <br />
                        , ,     <br />
                           .
                    </p>
                </div>
                <div className={style.secImg}>
                    <img src={sec05Img} />
                </div>
            </div>
        </div>
    )
}

export default Section05;